.filters__box{position:absolute;top:70px;left:0;display:flex;max-width:200px;width:100%;height:fit-content;justify-content:center;border-right:1px dotted rgba(68,68,68,.5);transition:all .5s}.filters__content{width:100%;padding:0 15px 15px}.filters__title{margin-bottom:30px}.filters__price-value{display:flex;justify-content:space-between;margin-bottom:10px}.filters__open-btn{visibility:hidden;opacity:0;background:#aabcb5;position:fixed;border:1px solid #444;border-left:none;border-radius:0 24px 24px 0;width:48px;height:96px;top:50%;transform:translateY(-50%);left:-1px;transition:all .5s;z-index:100;cursor:pointer}.filters__open-btn::before{content:"✦";position:absolute;font-size:48px;color:#aabcb5;top:-36%;left:-53%}.filters__open-btn::after{content:"✦";position:absolute;font-size:48px;color:#aabcb5;bottom:-30%;left:-53%}@media(max-width: 1520px){.filters__box{top:137px;left:-1px;padding:70px 0 0;position:fixed;background:#aabcb5;border:1px solid #444;border-radius:0 24px 24px 0;height:80%;z-index:100}.filters__box::before{content:"✦";position:absolute;font-size:48px;color:#aabcb5;top:-4.4%;left:-11%}.filters__box::after{content:"✦";position:absolute;font-size:48px;color:#aabcb5;bottom:-3.4%;left:-11%}.filters__open-btn{visibility:visible;opacity:1}}